body {
  font-family: "Rubik", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 1.5rem;
  font-weight: 500;
  margin: 0;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Rubik", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

body {
  background: #282C41;
}

.header .navegacion {
  display: flex;
  justify-content: space-between;
  margin: 1rem;
}
.header .navegacion .contenedor-imagen {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 2rem;
}
.header .navegacion .contenedor-imagen .imagen {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-image: url(../../../build/img/definicion-de-persona-min.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.header h1 {
  margin: -3rem 1rem;
  text-align: left;
  font-size: 3.5rem;
  color: #FFFFFF;
}

.main {
  margin-top: 5rem;
}
.main h3 {
  color: #727584;
  text-align: left;
  margin-left: 2rem;
  font-size: 2rem;
  margin-bottom: 0;
}
.main p {
  color: #FFFFFF;
}
.main .contenedor-imagenes-primero {
  display: grid;
  gap: 1.6666666667rem;
  grid-template-columns: repeat(2, 1fr);
  margin: 0 2rem;
}
.main .contenedor-imagenes-primero .imagenes {
  display: flex;
  align-items: flex-end;
  border-radius: 1.6666666667rem;
  background-size: cover;
  background-position: center;
  height: 25rem;
}
.main .contenedor-imagenes-primero .imagenes p {
  margin: 0 1rem;
  font-size: 1.8rem;
}
.main .contenedor-imagenes-primero .imagen-1 {
  background-image: url(../../../build/img/1.webp);
  grid-row: 1/3;
}
.main .contenedor-imagenes-primero .imagen-2 {
  background-image: url(../../../build/img/2.webp);
  height: 10rem;
  grid-row: 1/2;
}
.main .contenedor-imagenes-primero .imagen-3 {
  background-image: url(../../../build/img/3.webp);
  height: 25rem;
  grid-row: 2/4;
}
.main .contenedor-imagenes-primero .imagen-4 {
  background-image: url(../../../build/img/4.webp);
  height: 10rem;
  grid-row: 3/4;
}
.main .contenedor-imagenes-primero .imagen-5 {
  background-image: url(../../../build/img/5.webp);
  height: 20rem;
  grid-row: 4/6;
}
.main .contenedor-imagenes-primero .imagen-6 {
  background-image: url(../../../build/img/6.webp);
  height: 20rem;
  grid-column: 2/3;
}
@media (min-width: 768px) {
  .main .contenedor-imagenes-primero .imagenes {
    height: 50rem;
  }
  .main .contenedor-imagenes-primero .imagenes p {
    font-size: 2.5rem;
  }
  .main .contenedor-imagenes-primero .imagen-1 {
    height: 55rem;
  }
  .main .contenedor-imagenes-primero .imagen-2 {
    height: 25rem;
  }
  .main .contenedor-imagenes-primero .imagen-4 {
    height: 25rem;
  }
  .main .contenedor-imagenes-primero .imagen-3 {
    height: 55rem;
  }
  .main .contenedor-imagenes-primero .imagen-5 {
    height: 40rem;
  }
  .main .contenedor-imagenes-primero .imagen-6 {
    height: 40rem;
  }
}

.contenedor-titulos {
  display: flex;
  align-items: center;
}
.contenedor-titulos .formulario-ciudades {
  background: transparent;
  padding: 0.5rem 1rem;
  border-radius: 5rem;
  border: 1px solid #727584;
  color: #727584;
}